import { ProgressBar } from "react-bootstrap";
import { useGarageContext } from "../Garage";

const VehicleStats = ({ vehicle }) => {
  const { fuel, engine, body } = vehicle;
  const { locale } = useGarageContext();

  const getColourFromVal = (val) => {
    if (val >= 0 && val <= 0.25) return "danger";
    if (val > 0.25 && val < 0.75) return "warning";
    return "success";
  };

  return (
    <div className="mb-4">
      <div className="d-flex align-items-center">
        <div className="w-25 text-muted">{locale.fuel}</div>
        <div className="flex-fill">
          <ProgressBar
            variant={getColourFromVal(parseInt(fuel) / 100)}
            animated
            now={(parseInt(fuel) / 100) * 100}
            label={`${parseInt((parseInt(fuel) / 100) * 100)}%`}
          />
        </div>
      </div>
      <div className="d-flex align-items-center">
        <div className="w-25 text-muted">{locale.engine}</div>
        <div className="flex-fill">
          <ProgressBar
            variant={getColourFromVal(parseInt(engine) / 1000)}
            animated
            now={(parseInt(engine) / 1000) * 100}
            label={`${parseInt((parseInt(engine) / 1000) * 100)}%`}
          />
        </div>
      </div>
      <div className="d-flex align-items-center">
        <div className="w-25 text-muted">{locale.body}</div>
        <div className="flex-fill">
          <ProgressBar
            variant={getColourFromVal(parseInt(body) / 1000)}
            animated
            now={(parseInt(body) / 1000) * 100}
            label={`${parseInt((parseInt(body) / 1000) * 100)}%`}
          />
        </div>
      </div>
    </div>
  );
};

export default VehicleStats;
