import { Accordion, Badge } from "react-bootstrap";
import { useImpoundContext } from "../Impound";

const VehicleHeader = ({ vehicle }) => {
  const { plate, impound_data, vehicleLabel } = vehicle;
  const { hasWhitelistedJob, locale, config } = useImpoundContext();

  const impoundMetadata = JSON.parse(impound_data || "{}");
  const isAvailable = new Date() > new Date(impoundMetadata.retrieval_date);
  const availableDate = new Date(
    impoundMetadata.retrieval_date
  ).toLocaleDateString(config.DateFormat, {
    day: "numeric",
    month: "long",
    hour: "numeric",
    minute: "numeric",
    timeZoneName: "short",
  });

  return (
    <Accordion.Header>
      <div className="d-flex flex-fill justify-content-between">
        <div>
          <span className="me-2">{vehicleLabel}</span>
          <Badge bg="dark" text="warning" style={{ fontFamily: "monospace" }}>
            {plate}
          </Badge>
        </div>
        <div className="me-3">
          {!hasWhitelistedJob &&
            (isAvailable ? (
              <Badge bg="success">{locale.impoundAvailable}</Badge>
            ) : (
              <Badge bg="warning">
                {locale.impoundAvailable} {availableDate}
              </Badge>
            ))}
        </div>
      </div>
    </Accordion.Header>
  );
};

export default VehicleHeader;
