import { Button, Form, InputGroup, Modal } from "react-bootstrap";
import { useAppContext } from "../../AppContext";
import JgBadge from "../jg-badge/JgBadge";
import PGaragesTable from "./PGaragesTable";
import { useEffect, useMemo, useState } from "react";
import CreateEditModal from "./CreateEditModal";

const PrivateGarages = () => {
  const { event, locale, onCloseModal } = useAppContext();
  const [garages, setGarages] = useState([]);
  const [query, setQuery] = useState("");
  const [showCreateModal, setShowCreateModal] = useState(false);

  useEffect(() => {
    if (event.garages) setGarages(event.garages || []);
  }, [event.garages]);

  const filteredGarages = garages.filter(({ name, owners }) => {
    const keywords = query
      .toLocaleLowerCase()
      .split(" ")
      .filter((kw) => kw)
      .map((kw) => kw.trim());

    return (
      keywords.filter((kw) =>
        `${name} ${owners}`.toLocaleLowerCase().includes(kw)
      ).length === keywords.length
    );
  });

  const columns = useMemo(
    () => [
      { Header: locale?.garageName, accessor: "name" },
      { Header: locale?.owners, accessor: "owners" },
      { Header: "", accessor: "_" },
    ],
    [locale]
  );

  if (event.type !== "showPrivGarages") return null;

  return (
    <Modal
      show={true}
      onHide={onCloseModal}
      backdrop={false}
      centered
      size="xl"
    >
      <Modal.Header closeButton>
        <Modal.Title as="h5">Private Garages</Modal.Title>
      </Modal.Header>
      <Modal.Body className="p-0 pb-0">
        <div className="search-bar p-3 d-flex gap-3">
          <InputGroup>
            <InputGroup.Text>
              <i className="bi-search" />
            </InputGroup.Text>
            <Form.Control
              type="search"
              value={query}
              onChange={(e) => setQuery(e.target.value)}
              placeholder={locale.privGarageSearch}
            />
          </InputGroup>
          <Button
            variant="success"
            className="flex-shrink-0"
            onClick={() => setShowCreateModal(true)}
          >
            <i className="bi-plus-lg me-1" /> {locale.createPrivateGarage}
          </Button>
        </div>

        <div className="p-3 pt-0">
          <PGaragesTable
            columns={columns}
            data={filteredGarages}
            setGarages={setGarages}
          />
        </div>

        <CreateEditModal
          type="add"
          show={showCreateModal}
          onHide={() => setShowCreateModal(false)}
          garage={{}}
          onUpdate={(newGarage) =>
            setGarages((garages) => [newGarage, ...garages])
          }
        />
      </Modal.Body>
      <Modal.Footer>
        <JgBadge />
      </Modal.Footer>
    </Modal>
  );
};

export default PrivateGarages;
