import { Button } from "react-bootstrap";
import CreateEditModal from "./CreateEditModal";
import { useState } from "react";
import DeleteConfirmModal from "./DeleteConfirmModal";
import { useAppContext } from "../../AppContext";

const PGarageRow = ({ garage, updateGarage, deleteGarage }) => {
  const { locale } = useAppContext();
  const [showEditModal, setShowEditModal] = useState(false);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const owners =
    typeof garage?.owners === "string"
      ? JSON.parse(garage?.owners || "[]")
      : garage?.owners;

  const onDelete = async () => {
    deleteGarage();
    await fetch(`https://jg-advancedgarages/delete-private-garage`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json; charset=UTF-8",
      },
      body: JSON.stringify({
        id: garage.id,
        name: garage.name,
        owners,
      }),
    });
  };

  return (
    <tr>
      <td className="align-middle">{garage.name}</td>
      <td className="align-middle">{owners.length}</td>
      <td className="align-middle d-flex gap-2 justify-content-end">
        {/* Edit */}
        <Button onClick={() => setShowEditModal(true)} size="sm" variant="dark">
          <i className="bi-pencil me-1" /> {locale.edit}
        </Button>
        <CreateEditModal
          type="edit"
          show={showEditModal}
          onHide={() => setShowEditModal(false)}
          garage={garage}
          onUpdate={(updatedValues) =>
            updateGarage({ ...garage, ...updatedValues })
          }
        />

        {/* Delete */}
        <Button
          onClick={() => setShowDeleteModal(true)}
          size="sm"
          variant="danger"
        >
          <i className="bi-trash me-1" /> {locale.delete}
        </Button>
        <DeleteConfirmModal
          show={showDeleteModal}
          onHide={() => setShowDeleteModal(false)}
          onConfirm={onDelete}
        />
      </td>
    </tr>
  );
};

export default PGarageRow;
