import { Badge, Button } from "react-bootstrap";
import { useImpoundContext } from "../Impound";

const VehicleImpoundActions = ({ vehicle }) => {
  const { plate, impound_data } = vehicle;
  const { hasWhitelistedJob, impoundId, onHide, config, locale } =
    useImpoundContext();

  const metadata = JSON.parse(impound_data || "{}");
  const isAvailable = new Date() > new Date(metadata.retrieval_date);

  const onImpoundTakeOut = async () => {
    await fetch(`https://jg-advancedgarages/impoundTakeOutVehicle`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json; charset=UTF-8",
      },
      body: JSON.stringify({ plate, impoundId }),
    });

    onHide();
  };

  const onImpoundReturn = async () => {
    await fetch(`https://jg-advancedgarages/impoundReturnVehicle`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json; charset=UTF-8",
      },
      body: JSON.stringify({ plate }),
    });

    onHide();
  };

  return (
    <div className="d-flex justify-content-between">
      {hasWhitelistedJob && (
        <Button variant="primary" onClick={onImpoundReturn}>
          <i className="bi-box-arrow-in-right me-2" />
          {locale.vehicleReturnToOwnersGarage}
        </Button>
      )}

      {(hasWhitelistedJob || isAvailable) && (
        <Button
          variant={hasWhitelistedJob ? "secondary" : "primary"}
          onClick={onImpoundTakeOut}
        >
          <i className="bi-arrow-right me-2" />
          {locale.vehicleTakeOut}
          {!hasWhitelistedJob && (
            <Badge bg="white" text="primary" className="ms-1">
              {config.CurrencySymbol}
              {metadata.retrieval_cost}
            </Badge>
          )}
        </Button>
      )}
    </div>
  );
};

export default VehicleImpoundActions;
