import { Alert, Button, Card } from "react-bootstrap";
import { useGarageContext } from "../Garage";
import { useEffect, useState } from "react";
import { DateTime } from "luxon";
import MakePaymentModal from "./MakePaymentModal";

const formatCurrency = (amount, config) => {
  try {
    return amount?.toLocaleString(config?.NumberAndDateFormat, {
      style: "currency",
      currency: config.Currency,
      maximumFractionDigits: amount % 1 === 0 ? 0 : 2,
    });
  } catch (error) {
    return "InvalidLocale";
  }
};

const VehicleFinancing = ({ vehicle }) => {
  const { financeData } = useGarageContext();
  const { config, locale } = financeData;
  const [showMakePaymentModal, setShowMakePaymentModal] = useState(false);
  const [nextPaymentCountdown, setNextPaymentCountdown] = useState("");
  const vehicleFinanceData = financeData.vehicles?.find(
    ({ plate }) => plate === vehicle.plate
  );

  useEffect(() => {
    const onLoadDate = DateTime.now().toMillis();

    const getDeliveryCountdown = () => {
      if (!vehicleFinanceData) return false;

      if (
        vehicleFinanceData.finance_data.payment_failed &&
        vehicleFinanceData.finance_data.seconds_to_repo
      ) {
        const time = DateTime.fromMillis(
          onLoadDate + vehicleFinanceData.finance_data.seconds_to_repo * 1000,
          {
            locale: config.Locale,
          }
        );

        setNextPaymentCountdown(time.toRelative());
      } else if (vehicleFinanceData.finance_data.seconds_to_next_payment) {
        const time = DateTime.fromMillis(
          onLoadDate +
            vehicleFinanceData.finance_data.seconds_to_next_payment * 1000,
          {
            locale: config.Locale,
          }
        );

        setNextPaymentCountdown(time.toRelative());
      }
    };

    let interval;
    getDeliveryCountdown();
    interval = setInterval(() => getDeliveryCountdown(), 1000);
    return () => clearInterval(interval);
  }, [config, vehicleFinanceData]);

  if (!vehicleFinanceData) return null;
  return (
    <Card className="mb-3">
      <Card.Body>
        {vehicleFinanceData.finance_data.payment_failed && (
          <Alert variant="danger">
            <i className="bi-exclamation-triangle-fill me-2" />
            {locale.repossessionWarning}
          </Alert>
        )}

        <div className="d-flex justify-content-between">
          <div className="flex-fill">
            <div className="d-flex justify-content-between mb-2">
              <div>
                <small>{locale.recurringPayment}</small>
                <h4 className="white">
                  {formatCurrency(
                    vehicleFinanceData.finance_data.recurring_payment,
                    config
                  )}
                </h4>
              </div>
              <div>
                <small>{locale.remainingBalance}</small>
                <h4 className="white">
                  {formatCurrency(
                    vehicleFinanceData.finance_data.total -
                      vehicleFinanceData.finance_data.paid,
                    config
                  )}
                </h4>
              </div>
              <div>
                <small>{locale.remainingPayments}</small>
                <h4 className="white">
                  {vehicleFinanceData.finance_data.payments_complete}
                  <small>
                    {" "}
                    / {vehicleFinanceData.finance_data.total_payments}
                  </small>
                </h4>
              </div>
            </div>

            <div className="mb-4">
              <small>
                <i className="bi-clock me-2" />
                {`${locale.paymentTakenEvery} `}
                <strong>
                  {vehicleFinanceData.finance_data.payment_interval}{" "}
                  {locale.hours}
                </strong>
                {`. ${
                  vehicleFinanceData.finance_data.payment_failed
                    ? locale.vehicleRepossessed
                    : locale.nextPayment
                } `}
                <strong>{nextPaymentCountdown}</strong>
              </small>
            </div>
          </div>

          <div className="d-flex flex-column ms-5">
            <Button
              size="sm"
              className="mb-2"
              variant="primary"
              onClick={() => setShowMakePaymentModal("payment")}
            >
              {locale.makePayment}
            </Button>
            <Button
              size="sm"
              variant="outline-primary"
              onClick={() => setShowMakePaymentModal("pay-in-full")}
            >
              {locale.payInFull}
            </Button>
            <MakePaymentModal
              show={showMakePaymentModal}
              onHide={() => setShowMakePaymentModal(false)}
              vehicle={vehicleFinanceData}
              formatCurrency={formatCurrency}
            />
          </div>
        </div>

        <div className="finance-container">
          <div className="finance-progress-bar">
            <div
              className="finance-progress-bar-inner bg-success"
              style={{
                width:
                  ((vehicleFinanceData.finance_data.payments_complete - 1) /
                    (vehicleFinanceData.finance_data.total_payments - 1)) *
                    100 +
                  "%",
              }}
            />
            <div className="points-container">
              {Array(vehicleFinanceData.finance_data.total_payments)
                .fill(true)
                .map((_, i) => (
                  <div className="point" key={i}>
                    <i
                      className={`${
                        i < vehicleFinanceData.finance_data.payments_complete
                          ? "bi-check-circle-fill text-success"
                          : vehicleFinanceData.finance_data.payment_failed &&
                            i ===
                              vehicleFinanceData.finance_data.payments_complete
                          ? "bi-exclamation-circle-fill text-danger"
                          : "bi-clock-fill"
                      }`}
                    />
                  </div>
                ))}
            </div>
          </div>
        </div>
      </Card.Body>
    </Card>
  );
};

export default VehicleFinancing;
