import { useEffect, createContext, useContext, useState } from "react";
import { Accordion, Form, InputGroup, Modal } from "react-bootstrap";
import { useAppContext } from "../../AppContext";
import JgBadge from "../jg-badge/JgBadge";
import Vehicle from "./vehicle/Vehicle";

const GarageContext = createContext();

const Garage = () => {
  const { event, onCloseModal } = useAppContext();
  const [showGarage, setShowGarage] = useState(false);
  const [garageId, setGarageId] = useState("");
  const [garageType, setGarageType] = useState("");
  const [garageVariant, setGarageVariant] = useState("");
  const [vehicles, setVehicles] = useState([]);
  const [query, setQuery] = useState("");
  const [allPlayers, setAllPlayers] = useState([]);
  const [allGarages, setAllGarages] = useState([]);
  const [financeData, setFinanceData] = useState(false);
  const [config, setConfig] = useState({});
  const [locale, setLocale] = useState({});

  useEffect(() => {
    if (event.type === "showGarage") {
      setShowGarage(true);
      setVehicles([...event.vehicles]);
      setGarageId(event.garageId);
      setGarageType(event.garageType);
      setGarageVariant(
        event.jobGarage
          ? "job"
          : event.gangGarage
          ? "gang"
          : event.houseGarage
          ? "house"
          : "public"
      );
      setAllGarages(event.allGarages);
      setAllPlayers(event.allPlayers);
      setConfig(event.config);
      setLocale(event.locale);
    }
  }, [event]);

  useEffect(() => {
    const getFinanceData = async () => {
      if (!event.jgDealershipsStarted) return false;

      const res = await fetch(`https://jg-dealerships/get-financed-vehicles`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json; charset=UTF-8",
        },
      });
      const data = await res.json();
      setFinanceData(data);
    };

    getFinanceData();
  }, [event.jgDealershipsStarted]);

  const filteredVehicles = vehicles
    .filter(({ nickname, vehicleLabel, plate }) => {
      const keywords = query
        .toLocaleLowerCase()
        .split(" ")
        .filter((kw) => kw)
        .map((kw) => kw.trim());

      return (
        keywords.filter((kw) =>
          `${nickname || ""} ${vehicleLabel} ${plate}`
            .toLocaleLowerCase()
            .includes(kw)
        ).length === keywords.length
      );
    })
    .reduce(
      (acc, v) =>
        v.garage_id === garageId && !v.impound ? [v, ...acc] : [...acc, v],
      []
    );

  const onHide = () => {
    setShowGarage(false);
    onCloseModal();
    setQuery("");
  };

  return (
    <GarageContext.Provider
      value={{
        vehicles,
        garageId,
        garageType,
        garageVariant,
        financeData,
        setFinanceData,
        allGarages,
        allPlayers,
        config,
        locale,
        onHide,
      }}
    >
      <Modal
        show={showGarage}
        onHide={onHide}
        backdrop={false}
        centered
        size="lg"
        scrollable={true}
      >
        <Modal.Header closeButton>
          <Modal.Title as="h5">
            <span className="text-muted p-1 pe-3">
              {garageType === "car" && <i className="bi-car-front" />}
              {garageType === "sea" && <i className="bi-water" />}
              {garageType === "air" && <i className="bi-airplane" />}
            </span>
            <span>{garageId}</span>
          </Modal.Title>
        </Modal.Header>
        <Modal.Body className="p-0 pb-2">
          <div className="search-bar p-3">
            <InputGroup>
              <InputGroup.Text>
                <i className="bi-search" />
              </InputGroup.Text>
              <Form.Control
                type="search"
                value={query}
                onChange={(e) => setQuery(e.target.value)}
                placeholder={locale.search}
              />
            </InputGroup>
          </div>

          {!vehicles.length ? (
            <div className="p-3 text-center">{locale.noVehicles}</div>
          ) : (
            <Accordion defaultActiveKey="0">
              {filteredVehicles.map((vehicle) => (
                <Accordion.Item
                  eventKey={vehicle.id}
                  key={vehicle.id}
                  onKeyUp={(e) => e.preventDefault()}
                >
                  <Vehicle vehicle={vehicle} />
                </Accordion.Item>
              ))}
            </Accordion>
          )}
        </Modal.Body>
        <Modal.Footer className="d-flex justify-content-between text-muted">
          <span>
            {vehicles.length} {locale.vehicles}
          </span>
          <JgBadge />
        </Modal.Footer>
      </Modal>
    </GarageContext.Provider>
  );
};

const useGarageContext = () => useContext(GarageContext);

export { Garage, useGarageContext };
