import { Button, Form, Modal } from "react-bootstrap";
import { useAppContext } from "../../AppContext";
import { useState } from "react";

const AddOwnerModal = ({ show, onHide, currentOwners, onAddOwner }) => {
  const { event, locale } = useAppContext();
  const [playerIdentifier, setPlayerIdentifier] = useState("");
  const onlinePlayers = event.allPlayers.filter(
    ({ identifier }) =>
      !currentOwners.find((owner) => owner.identifier === identifier)
  );

  const onSubmit = async () => {
    if (!playerIdentifier) return false;

    onAddOwner(
      event.allPlayers.find(({ identifier }) => identifier === playerIdentifier)
    );
    setPlayerIdentifier("");
    onHide();
  };

  return (
    <Modal show={show} onHide={onHide} centered style={{ zIndex: 99999 }}>
      <Modal.Header closeButton />
      <Modal.Body>
        <Form.Group className="mb-3">
          <Form.Label>{locale.player}</Form.Label>
          <Form.Select
            value={playerIdentifier}
            onChange={(e) => setPlayerIdentifier(e.target.value)}
          >
            <option value="">Select player</option>
            {onlinePlayers.map(({ identifier, name }) => (
              <option key={identifier} value={identifier}>
                {name}
              </option>
            ))}
          </Form.Select>
        </Form.Group>
        <Button
          onClick={onSubmit}
          variant="primary"
          disabled={!playerIdentifier}
        >
          {locale.addPlayer}
        </Button>
      </Modal.Body>
    </Modal>
  );
};

export default AddOwnerModal;
