import { Card } from "react-bootstrap";
import { useGarageContext } from "../Garage";

const VehicleImpoundData = ({ vehicle }) => {
  const { impound_data, impound_retrievable } = vehicle;
  const { locale } = useGarageContext();
  const impoundMetadata = JSON.parse(impound_data || "{}");

  return (
    <Card border="danger">
      <Card.Header>{locale.impoundInformation}</Card.Header>
      <Card.Body>
        <p>
          <span className="text-muted">{locale.impoundedBy}: </span>
          {impoundMetadata.charname}
        </p>
        <p>
          <span className="text-muted">{locale.impoundedReason}: </span>
          {impoundMetadata.reason || locale.impoundNoReason}
        </p>

        {impound_retrievable ? (
          <p className="text-success">{locale.impoundPlayerCanCollect}</p>
        ) : (
          <p className="text-danger">
            {locale.impoundCollectionContact.replace(
              "%{value}",
              impoundMetadata.charname
            )}
          </p>
        )}
      </Card.Body>
    </Card>
  );
};

export default VehicleImpoundData;
