import { useEffect, createContext, useContext, useState } from "react";
import { Accordion, Form, InputGroup, Modal } from "react-bootstrap";
import { useAppContext } from "../../AppContext";
import JgBadge from "../jg-badge/JgBadge";
import Vehicle from "./vehicle/Vehicle";

const ImpoundContext = createContext();

const Impound = () => {
  const { event, onCloseModal } = useAppContext();
  const [showImpound, setShowImpound] = useState(false);
  const [impoundId, setImpoundId] = useState(false);
  const [impoundType, setImpoundType] = useState("car");
  const [hasWhitelistedJob, setHasWhitelistedJob] = useState(false);
  const [vehicles, setVehicles] = useState([]);
  const [query, setQuery] = useState("");
  const [config, setConfig] = useState({});
  const [locale, setLocale] = useState({});

  useEffect(() => {
    if (event.type === "showImpound") {
      setShowImpound(true);
      setImpoundId(event.impoundId);
      setImpoundType(event.impoundType);
      setHasWhitelistedJob(event.isWhitelist);
      setVehicles(event.vehicles);
      setConfig(event.config);
      setLocale(event.locale);
    }
  }, [event]);

  const onHide = () => {
    setShowImpound(false);
    onCloseModal();
    setQuery("");
  };

  const filteredVehicles = vehicles.filter(({ vehicleLabel, plate }) => {
    const keywords = query
      .toLocaleLowerCase()
      .split(" ")
      .filter((kw) => kw)
      .map((kw) => kw.trim());

    return (
      keywords.filter((kw) =>
        `${vehicleLabel} ${plate}`.toLocaleLowerCase().includes(kw)
      ).length === keywords.length
    );
  });

  return (
    <ImpoundContext.Provider
      value={{ impoundId, hasWhitelistedJob, vehicles, config, locale, onHide }}
    >
      <Modal
        show={showImpound}
        onHide={onHide}
        backdrop={false}
        centered
        size="lg"
        scrollable={true}
      >
        <Modal.Header closeButton>
          <Modal.Title as="h5">
            <span className="text-muted p-1 pe-3">
              {impoundType === "car" && <i className="bi-car-front" />}
              {impoundType === "sea" && <i className="bi-water" />}
              {impoundType === "air" && <i className="bi-airplane" />}
            </span>
            {locale.impound}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body className="p-0 pb-2">
          <div className="search-bar p-3">
            <InputGroup>
              <InputGroup.Text>
                <i className="bi-search" />
              </InputGroup.Text>
              <Form.Control
                type="search"
                value={query}
                onChange={(e) => setQuery(e.target.value)}
                placeholder={locale.search}
              />
            </InputGroup>
          </div>

          {!vehicles.length ? (
            <div className="p-3 text-center">{locale.impoundNoVehicles}</div>
          ) : (
            <Accordion defaultActiveKey="0" flush>
              {filteredVehicles.map((vehicle, i) => (
                <Accordion.Item eventKey={i} key={i}>
                  <Vehicle vehicle={vehicle} />
                </Accordion.Item>
              ))}
            </Accordion>
          )}
        </Modal.Body>
        <Modal.Footer className="d-flex justify-content-between text-muted">
          <span>
            {vehicles.length} {locale.vehicles}
          </span>
          <JgBadge />
        </Modal.Footer>
      </Modal>
    </ImpoundContext.Provider>
  );
};

const useImpoundContext = () => useContext(ImpoundContext);

export { Impound, useImpoundContext };
