import {
  Button,
  Card,
  Col,
  Form,
  InputGroup,
  Modal,
  Row,
} from "react-bootstrap";
import { useAppContext } from "../../AppContext";
import { useFormik } from "formik";
import AddOwnerModal from "./AddOwnerModal";
import { useState } from "react";

const CreateEditModal = ({ type, show, onHide, garage, onUpdate }) => {
  const { event, locale } = useAppContext();
  const [showAddOwnerModal, setShowAddOwnerModal] = useState(false);
  const formik = useFormik({
    initialValues: {
      name: garage.name || "",
      x: garage.x,
      y: garage.y,
      z: garage.z,
      h: garage.h,
      distance: garage.distance || 10,
      owners: garage.owners
        ? typeof garage.owners === "string"
          ? JSON.parse(garage.owners || "[]")
          : garage.owners
        : [],
    },
    onSubmit: async (values) => {
      if (type === "edit") {
        await fetch(`https://jg-advancedgarages/edit-private-garage`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json; charset=UTF-8",
          },
          body: JSON.stringify({
            id: garage.id,
            ...values,
          }),
        });
        onUpdate(values);
      } else if (type === "add") {
        if (event.garages.find(({ name }) => name === values.name)) {
          return formik.setErrors({ name: locale.garageNameExistsError });
        }

        const res = await fetch(
          `https://jg-advancedgarages/create-private-garage`,
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json; charset=UTF-8",
            },
            body: JSON.stringify(values),
          }
        );
        const data = await res.json();
        onUpdate({ ...values, id: data.id });
        formik.resetForm({});
      }

      onHide();
    },
  });

  const getCurrentCoords = async () => {
    const res = await fetch(`https://jg-advancedgarages/get-current-coords`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json; charset=UTF-8",
      },
    });
    const data = await res.json();
    formik.setFieldValue("x", data.x);
    formik.setFieldValue("y", data.y);
    formik.setFieldValue("z", data.z);
    formik.setFieldValue("h", data.h);
  };

  return (
    <Modal
      show={show}
      onHide={onHide}
      size="lg"
      centered
      style={{ zIndex: 9999 }}
    >
      <Modal.Header closeButton>
        <Modal.Title as="h5">
          {type === "edit"
            ? locale.editPrivateGarage
            : locale.createPrivateGarage}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form onSubmit={formik.handleSubmit}>
          <Form.Group className="mb-3">
            <Form.Label>{locale.garageName}</Form.Label>
            <Form.Control
              type="text"
              id="name"
              value={formik.values.name}
              onChange={formik.handleChange}
              isInvalid={formik.touched.name && formik.errors.name}
              required
            />

            <Form.Control.Feedback type="invalid">
              {formik.errors.name}
            </Form.Control.Feedback>
          </Form.Group>

          <Row>
            <Col>
              <Form.Group className="mb-3">
                <Form.Label>{locale.location} (x, y, z, heading)</Form.Label>
                <InputGroup>
                  <Form.Control
                    type="number"
                    id="x"
                    value={formik.values.x}
                    onChange={formik.handleChange}
                    placeholder="x"
                    required
                  />
                  <Form.Control
                    type="number"
                    id="y"
                    value={formik.values.y}
                    onChange={formik.handleChange}
                    placeholder="y"
                    required
                  />
                  <Form.Control
                    type="number"
                    id="z"
                    value={formik.values.z}
                    onChange={formik.handleChange}
                    placeholder="z"
                    required
                  />
                  <Form.Control
                    type="number"
                    id="h"
                    value={formik.values.h}
                    onChange={formik.handleChange}
                    placeholder="Heading"
                    required
                  />
                </InputGroup>
              </Form.Group>
            </Col>
            <Col sm={3}>
              <Form.Group className="mb-3">
                <Form.Label>Radius</Form.Label>
                <Form.Control
                  type="number"
                  id="distance"
                  value={formik.values.distance}
                  onChange={formik.handleChange}
                  required
                />
              </Form.Group>
            </Col>
          </Row>

          <div className="d-flex justify-content-end mb-3">
            <Button
              variant="outline-primary"
              size="sm"
              onClick={getCurrentCoords}
            >
              <i className="bi-geo-fill" /> Get current coordinates
            </Button>
          </div>

          <Card className="mb-3">
            <Card.Header>{locale.owners}</Card.Header>
            <Card.Body>
              {!formik.values.owners.length ? (
                locale.noPlayers
              ) : (
                <table style={{ width: "100%" }}>
                  <thead>
                    <tr>
                      <th>{locale.identifier}</th>
                      <th>{locale.name}</th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    {formik.values.owners.map(({ identifier, name }) => (
                      <tr key={identifier}>
                        <td
                          style={{
                            maxWidth: "20px",
                            whiteSpace: "nowrap",
                            overflow: "hidden",
                            textOverflow: "ellipsis",
                          }}
                        >
                          <code>{identifier}</code>
                        </td>
                        <td>{name}</td>
                        <td style={{ float: "right" }}>
                          <Button
                            size="sm"
                            variant="danger"
                            onClick={() =>
                              formik.setFieldValue(
                                "owners",
                                formik.values.owners.filter(
                                  (owner) => owner.identifier !== identifier
                                )
                              )
                            }
                          >
                            Remove
                          </Button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              )}

              <AddOwnerModal
                show={showAddOwnerModal}
                onHide={() => setShowAddOwnerModal(false)}
                currentOwners={formik.values.owners}
                onAddOwner={(newOwner) =>
                  formik.setFieldValue("owners", [
                    newOwner,
                    ...formik.values.owners,
                  ])
                }
              />
            </Card.Body>
            <Card.Footer className="d-flex justify-content-end">
              <Button
                variant="outline-primary"
                size="sm"
                onClick={() => setShowAddOwnerModal(true)}
              >
                <i className="bi-plus" /> Add Player
              </Button>
            </Card.Footer>
          </Card>

          <Button type="submit">{locale.save}</Button>
        </Form>
      </Modal.Body>
    </Modal>
  );
};

export default CreateEditModal;
