import { Badge, Button } from "react-bootstrap";
import { useGarageContext } from "../Garage";
import { useEffect, useRef, useState } from "react";

const VehicleActions = ({ vehicle, setShowTransfer }) => {
  const {
    plate,
    vehicle: vehicleSpawnCode,
    isAlreadyOut = false,
    garage_id: vehGarageId,
    in_garage,
  } = vehicle;
  const { garageId, garageVariant, config, onHide, locale } =
    useGarageContext();
  const loadingInputBlocker = useRef();
  const [loadingVehicle, setLoadingVehicle] = useState(false);

  const onTakeOut = async () => {
    setLoadingVehicle(true);

    let endpoint = "garageTakeOutVehicle";
    if (garageVariant === "job") endpoint = "jobGarageTakeOutVehicle";
    if (garageVariant === "gang") endpoint = "gangGarageTakeOutVehicle";

    try {
      await fetch(`https://jg-advancedgarages/${endpoint}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json; charset=UTF-8",
        },
        body: JSON.stringify({
          vehicle: vehicleSpawnCode,
          plate,
          garageId: garageId,
        }),
      });
    } catch (error) {}

    onHide();
    setLoadingVehicle(false);
  };

  useEffect(() => {
    if (loadingVehicle) loadingInputBlocker.current.focus();
  }, [loadingVehicle]);

  return (
    <>
      {/* Prevents user clicking on shit, potentially respawning the vehicle multiple times... */}
      {loadingVehicle && (
        <div
          ref={loadingInputBlocker}
          style={{
            width: "100vw",
            height: "100vh",
            position: "fixed",
            top: 0,
            left: 0,
            zIndex: 99999999,
            cursor: "default",
            color: "transparent",
          }}
          contentEditable
          onKeyDown={(e) => e.stopPropagation()}
        />
      )}

      <div className="d-flex justify-content-between">
        {loadingVehicle ? (
          <Button variant="primary" disabled>
            {locale.loadingVehicle}
          </Button>
        ) : isAlreadyOut ? (
          <Button variant="danger" disabled>
            <i className="bi-dash-circle-dotted me-2" />
            {locale.vehicleNotInGarage}
          </Button>
        ) : vehGarageId === garageId ||
          !["public", "house"].includes(garageVariant) ? (
          <Button variant="primary" onClick={onTakeOut}>
            <i className="bi-arrow-right me-2" />
            {in_garage ? (
              locale.vehicleTakeOut
            ) : (
              <span>
                <span>{locale.vehicleReturnAndTakeOut}</span>
                <Badge bg="white" text="primary" className="ms-1">
                  {config.GarageVehicleReturnCost > 0 &&
                    `${config.CurrencySymbol}${config.GarageVehicleReturnCost}`}
                </Badge>
              </span>
            )}
          </Button>
        ) : (
          <Button variant="primary" disabled>
            <i className="bi-slash-circle me-2" />
            {locale.inGarage} {vehGarageId}
          </Button>
        )}

        {!isAlreadyOut &&
          (config.EnableTransfers.betweenGarages ||
            config.EnableTransfers.betweenPlayers) &&
          ["public", "house"].includes(garageVariant) && (
            <Button
              variant="secondary"
              onClick={() => setShowTransfer((st) => !st)}
            >
              <i className="bi-arrow-left-right me-2" />
              {locale.vehicleTransfer}
            </Button>
          )}
      </div>
    </>
  );
};

export default VehicleActions;
