import { useState } from "react";
import { Accordion } from "react-bootstrap";
import VehicleHeader from "./VehicleHeader";
import VehicleImpoundData from "./VehicleImpoundData";
import VehicleStats from "./VehicleStats";
import VehicleActions from "./VehicleActions";
import VehicleTransfer from "./VehicleTransfer";
import VehicleFinancing from "./VehicleFinancing";
import { useGarageContext } from "../Garage";

const Vehicle = ({ vehicle }) => {
  const { financeData } = useGarageContext();
  const [showTransfer, setShowTransfer] = useState(false);

  return (
    <>
      <VehicleHeader vehicle={vehicle} />

      <Accordion.Body>
        <VehicleStats vehicle={vehicle} />

        {vehicle.impound ? (
          <VehicleImpoundData vehicle={vehicle} />
        ) : (
          <div>
            {financeData && <VehicleFinancing vehicle={vehicle} />}

            <VehicleActions
              vehicle={vehicle}
              setShowTransfer={setShowTransfer}
            />

            {showTransfer && (
              <VehicleTransfer
                vehicle={vehicle}
                setShowTransfer={setShowTransfer}
              />
            )}
          </div>
        )}
      </Accordion.Body>
    </>
  );
};

export default Vehicle;
