import { Button, Modal } from "react-bootstrap";
import { useGarageContext } from "../Garage";

const MakePaymentModal = ({ show, onHide, vehicle, formatCurrency }) => {
  const {
    financeData: { config, locale },
    setFinanceData,
  } = useGarageContext();

  const onConfirm = async () => {
    try {
      await fetch(`https://jg-dealerships/finance-make-payment`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json; charset=UTF-8",
        },
        body: JSON.stringify({
          plate: vehicle.plate,
          type: show,
        }),
      });

      const res = await fetch(`https://jg-dealerships/get-financed-vehicles`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json; charset=UTF-8",
        },
      });
      const data = await res.json();
      setFinanceData(data);
    } catch (error) {}
    onHide();
  };

  return (
    <Modal
      show={!!show}
      onHide={() => onHide()}
      style={{ zIndex: 9999 }}
      centered
    >
      <Modal.Header closeButton>{locale.makePayment}</Modal.Header>
      <Modal.Body>
        {`${locale.earlyPaymentConfirmation} `}
        {formatCurrency(
          show === "payment"
            ? vehicle.finance_data.recurring_payment
            : show === "pay-in-full"
            ? vehicle.finance_data.total - vehicle.finance_data.paid
            : 0,
          config
        )}
        ?
      </Modal.Body>
      <Modal.Footer>
        <Button variant="primary" onClick={onConfirm}>
          {locale.yes}
        </Button>
        <Button variant="secondary" onClick={() => onHide()}>
          {locale.no}
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default MakePaymentModal;
