import { useEffect, useState } from "react";
import { Button, Form, InputGroup, Modal } from "react-bootstrap";
import { useAppContext } from "../../AppContext";
import JgBadge from "../jg-badge/JgBadge";

// eslint-disable-next-line no-extend-native
Date.prototype.addHours = function (h) {
  this.setTime(this.getTime() + h * 60 * 60 * 1000);
  return this;
};

const ImpoundForm = () => {
  const { event, onCloseModal } = useAppContext();
  const [showImpoundForm, setShowImpoundForm] = useState(false);
  const [vehiclePlate, setVehiclePlate] = useState("");
  const [config, setConfig] = useState({});
  const [reason, setReason] = useState("");
  const [retrievable, setRetrievable] = useState(false);
  const [impoundTime, setImpoundTime] = useState("0");
  const [cost, setCost] = useState("");
  const [locale, setLocale] = useState({});

  useEffect(() => {
    if (event.type === "showImpoundForm") {
      setShowImpoundForm(true);
      setVehiclePlate(event.plate);
      setReason("");
      setRetrievable(false);
      setImpoundTime("0");
      setCost("");
      setConfig(event.config);
      setLocale(event.locale);
    }
  }, [event]);

  const onFormSubmit = async (e) => {
    e.preventDefault();

    await fetch(`https://jg-advancedgarages/impoundVehicle`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json; charset=UTF-8",
      },
      body: JSON.stringify({
        plate: vehiclePlate,
        reason,
        retrievable,
        retrievalCost: parseInt(cost || "0"),
        retrievalDate: new Date()
          .addHours(parseInt(impoundTime || "0"))
          .toString(),
      }),
    });

    onHide();
  };

  const onHide = () => {
    setShowImpoundForm(false);
    onCloseModal();
  };

  return (
    <Modal show={showImpoundForm} onHide={onHide} backdrop={false} centered>
      <Form onSubmit={onFormSubmit}>
        <Modal.Header closeButton>
          <Modal.Title as="h5">{locale.impoundVehicle}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="mb-3">
            <Form.Label>{locale.vehiclePlate}</Form.Label>
            <Form.Control type="text" value={vehiclePlate} readOnly disabled />
          </div>
          <div className="mb-3">
            <Form.Label>{locale.impoundReasonField}</Form.Label>
            <Form.Control
              type="text"
              value={reason}
              onChange={(e) => setReason(e.target.value)}
            />
          </div>
          <div className="mb-3">
            <Form.Check
              type="checkbox"
              id="retrievable"
              label={locale.impoundRetrievableByOwner}
              checked={retrievable}
              onChange={(e) => setRetrievable(e.target.checked)}
            />
          </div>

          {retrievable && (
            <div>
              <div className="mb-3">
                <Form.Label>{locale.impoundTime}</Form.Label>
                <Form.Select
                  value={impoundTime}
                  onChange={(e) => setImpoundTime(e.target.value)}
                  required
                >
                  <option value="0">
                    {locale.impoundAvailableImmediately}
                  </option>
                  <option value="1">1 {locale.hour}</option>
                  <option value="4">4 {locale.hours}</option>
                  <option value="12">12 {locale.hours}</option>
                  <option value="24">1 {locale.day}</option>
                  <option value="72">3 {locale.days}</option>
                  <option value="168">7 {locale.days}</option>
                </Form.Select>
              </div>

              <div className="mb-3">
                <Form.Label>{locale.impoundCost}</Form.Label>
                <InputGroup>
                  <InputGroup.Text>{config.CurrencySymbol}</InputGroup.Text>
                  <Form.Control
                    type="number"
                    value={cost}
                    onChange={(e) => setCost(e.target.value)}
                    required
                    min={0}
                  />
                </InputGroup>
              </div>
            </div>
          )}
        </Modal.Body>
        <Modal.Footer className="d-flex justify-content-between text-muted">
          <Button variant="success" type="submit">
            {locale.impoundVehicle}
          </Button>
          <JgBadge />
        </Modal.Footer>
      </Form>
    </Modal>
  );
};

export default ImpoundForm;
