import { useEffect, useState } from "react";
import { Button, Form, Modal } from "react-bootstrap";
import { useAppContext } from "../../AppContext";
import JgBadge from "../jg-badge/JgBadge";

const VehiclePlateForm = () => {
  const { event, onCloseModal } = useAppContext();
  const [showPlateForm, setShowPlateForm] = useState(false);
  const [vehiclePlate, setVehiclePlate] = useState("");
  const [newPlate, setNewPlate] = useState("");
  const [locale, setLocale] = useState({});

  useEffect(() => {
    if (event.type === "showChangeVehiclePlate") {
      setShowPlateForm(true);
      setVehiclePlate(event.plate);
      setNewPlate("");
      setLocale(event.locale);
    }
  }, [event]);

  const onFormSubmit = async (e) => {
    e.preventDefault();

    await fetch(`https://jg-advancedgarages/changeVehiclePlate`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json; charset=UTF-8",
      },
      body: JSON.stringify({
        curPlate: vehiclePlate,
        newPlate: newPlate.toUpperCase().trim(),
      }),
    })
      .then((r) => r.json())
      .then((r) => {
        if (!r.error) onHide();
      });
  };

  const onHide = () => {
    setShowPlateForm(false);
    onCloseModal();
  };

  return (
    <Modal show={showPlateForm} onHide={onHide} backdrop={false} centered>
      <Form onSubmit={onFormSubmit}>
        <Modal.Header closeButton>
          <Modal.Title as="h5">{locale.changeVehiclePlate}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="mb-3">
            <Form.Label>{locale.vehiclePlate}</Form.Label>
            <Form.Control type="text" value={vehiclePlate} readOnly disabled />
          </div>
          <div className="mb-3">
            <Form.Label>{locale.newPlate}</Form.Label>
            <Form.Control
              type="text"
              value={newPlate}
              onChange={(e) => setNewPlate(e.target.value)}
              maxLength={8}
              pattern="^[a-zA-Z0-9 ]*"
              title="Only letters, numbers and spaces allowed"
              required
            />
          </div>
        </Modal.Body>
        <Modal.Footer className="d-flex justify-content-between text-muted">
          <Button variant="success" type="submit">
            {locale.changeVehiclePlate}
          </Button>
          <JgBadge />
        </Modal.Footer>
      </Form>
    </Modal>
  );
};

export default VehiclePlateForm;
