import { Button, ButtonGroup, Form, Modal } from "react-bootstrap";
import { useAppContext } from "../../AppContext";
import { useState } from "react";

const LiveriesExtrasMenu = () => {
  const { event, locale, onCloseModal } = useAppContext();
  const [tab, setTab] = useState("liveries");
  const availableExtras = event.extras?.filter(({ available }) => available);

  const onClose = () => {
    fetch("https://jg-advancedgarages/exit-liveries-extras-menu");
    setTab("liveries");
    onCloseModal();
  };

  const onToggleLivery = async (livery_id) => {
    await fetch(`https://jg-advancedgarages/toggle-livery`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json; charset=UTF-8",
      },
      body: JSON.stringify({
        livery_id: parseInt(livery_id),
      }),
    });
  };

  const onToggleExtra = async (extra_id, toggle) => {
    await fetch(`https://jg-advancedgarages/toggle-extra`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json; charset=UTF-8",
      },
      body: JSON.stringify({
        extra_id,
        disabled: toggle ? 0 : 1, // this is the value that SetVehicleExtra is expecting
      }),
    });
  };

  if (event.type !== "showLiveriesExtrasMenu") return null;

  return (
    <div
      className="modal show"
      style={{
        display: "block",
        position: "fixed",
        width: 300,
        height: "fit-content",
        bottom: 20,
        left: 20,
        top: "unset",
        padding: 0,
      }}
    >
      <Modal.Dialog style={{ margin: 0 }}>
        <Modal.Header closeButton onHide={onClose}>
          <i className="bi-tools me-2" />
          <Modal.Title as="h6">{locale.vehicleSetup}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <ButtonGroup style={{ width: "100%" }}>
            {["liveries", "extras"].map((type) => (
              <Button
                variant={tab === type ? "primary" : "dark"}
                onClick={() => setTab(type)}
              >
                {locale[type]}
              </Button>
            ))}
          </ButtonGroup>

          <div className="mt-3">
            {tab === "liveries" &&
              (!event.liveriesCount || event.liveriesCount < 1 ? (
                locale.noLiveries
              ) : (
                <div>
                  <div className="mt-2">
                    <Form.Check
                      id={`livery-none`}
                      name="vehicle-livery"
                      type="radio"
                      value={0}
                      label={`${locale.none}`}
                      defaultChecked={event.currentLivery === 0}
                      onChange={(e) => onToggleLivery(e.target.value)}
                    />
                  </div>

                  {Array(event.liveriesCount)
                    .fill()
                    .map((_, i) => (
                      <div className="mt-2">
                        <Form.Check
                          id={`livery-${i + 1}`}
                          name="vehicle-livery"
                          type="radio"
                          value={i + 1}
                          label={`${locale.livery} ${i + 1}`}
                          defaultChecked={event.currentLivery === i + 1}
                          onChange={(e) => onToggleLivery(e.target.value)}
                        />
                      </div>
                    ))}
                </div>
              ))}
          </div>
          <div>
            {tab === "extras" &&
              (!availableExtras.length
                ? locale.noExtras
                : availableExtras.map(({ id, enabled }) => (
                    <div className="mt-2">
                      <Form.Check
                        id={`extra-${id}`}
                        type="switch"
                        label={`${locale.extra} ${id}`}
                        defaultChecked={enabled}
                        onChange={(e) => onToggleExtra(id, e.target.checked)}
                      />
                    </div>
                  )))}
          </div>

          <Button
            size="lg"
            style={{ width: "100%" }}
            className="mt-3"
            onClick={onClose}
          >
            <i className="bi-arrow-right me-2" />
            {locale.vehicleTakeOut}
          </Button>
        </Modal.Body>
      </Modal.Dialog>
    </div>
  );
};

export default LiveriesExtrasMenu;
