import { Button, Form, Table } from "react-bootstrap";
import { useTable, usePagination } from "react-table";
import { useAppContext } from "../../AppContext";
import PGarageRow from "./PGarageRow";

const PGaragesTable = ({ data, columns, setGarages }) => {
  const { locale } = useAppContext();
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    page,
    canPreviousPage,
    canNextPage,
    pageOptions,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable(
    { columns, data, autoResetPage: false, autoResetFilters: false },
    usePagination
  );

  return (
    <div>
      <Table hover className="bootstrap-table" {...getTableProps()}>
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th {...column.getHeaderProps()}>{column.render("Header")}</th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {!page.length ? (
            <tr>
              <td colSpan={100} align="center">
                {locale.noPrivateGarages}
              </td>
            </tr>
          ) : (
            page.map((row) => {
              return (
                <PGarageRow
                  key={row.original.id}
                  garage={row.original}
                  updateGarage={(data) =>
                    setGarages((garages) =>
                      garages.map((garage) =>
                        garage.id === row.original.id ? data : garage
                      )
                    )
                  }
                  deleteGarage={() =>
                    setGarages((garages) =>
                      garages.filter(({ id }) => id !== row.original.id)
                    )
                  }
                />
              );
            })
          )}
        </tbody>
      </Table>

      <div className="pagination d-flex justify-content-between">
        <Button
          variant="primary"
          onClick={() => previousPage()}
          disabled={!canPreviousPage}
        >
          <i className="bi-arrow-left" /> {locale.previous}
        </Button>
        <div className="d-flex align-items-center">
          <span className="me-3">
            {locale.page} {pageIndex + 1} {locale.of} {pageOptions.length}
          </span>
          <Form.Select
            size="sm"
            value={pageSize}
            onChange={(e) => {
              setPageSize(Number(e.target.value));
            }}
            style={{ width: 110 }}
          >
            {[10, 20, 30, 40, 50].map((pageSize) => (
              <option key={pageSize} value={pageSize}>
                {locale.show} {pageSize}
              </option>
            ))}
          </Form.Select>
        </div>
        <Button
          variant="primary"
          onClick={() => nextPage()}
          disabled={!canNextPage}
        >
          {locale.next} <i className="bi-arrow-right" />
        </Button>
      </div>
    </div>
  );
};

export default PGaragesTable;
