import { Badge } from "react-bootstrap";
import { useImpoundContext } from "../Impound";

const VehicleImpoundInfo = ({ vehicle }) => {
  const { impound_data, impound_retrievable } = vehicle;
  const { hasWhitelistedJob, locale, config } = useImpoundContext();

  const impoundMetadata = JSON.parse(impound_data || "{}");
  const availableDate = new Date(
    impoundMetadata.retrieval_date
  ).toLocaleDateString(config.DateFormat, {
    day: "numeric",
    month: "long",
    hour: "numeric",
    minute: "numeric",
    timeZoneName: "short",
  });

  return (
    <div>
      <p>
        <span className="text-muted">{locale.impoundedBy}: </span>
        {impoundMetadata.charname}
      </p>
      <p>
        <span className="text-muted">{locale.impoundedReason}: </span>
        {impoundMetadata.reason || locale.impoundNoReason}
      </p>

      {hasWhitelistedJob && (
        <p>
          <span className="text-muted">
            {locale.impoundRetrievableByOwner}:{" "}
          </span>
          {impound_retrievable ? (
            <span>
              {locale.yes}
              <Badge bg="warning" text="dark" className="ms-1">
                {locale.impoundAvailable} {availableDate}
              </Badge>
            </span>
          ) : (
            locale.no
          )}
        </p>
      )}
    </div>
  );
};

export default VehicleImpoundInfo;
